.top-branding-settings{
    text-align: center;
    color: #505050;
    padding: 15px;
}

#settings-brand{
    color: #383a90;
}
#link-brand-setting{
    text-decoration: none;
    color: #505050;
}
#active{
    background: rgba(0, 0, 0, .05);
}

.settings-navbar{
    margin-top: 35px;
    margin-bottom: 35px;
    display: flex;
    justify-content: center;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    transition: all .5s ease;
}

.links-nav-settings{
    padding: 25px;
    text-decoration: none;
    color: #505050;
    margin-right: 25px;
}

.links-nav-settings:hover{
    background: rgba(0, 0, 0, .05);
}
.icons-settings{
    position: relative;
    top: 2px;
}

.headline{
    color: #505050;
    text-align: center;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    border-bottom: 3px solid #383a60;
    padding: 5px;
}

.boxes{
    display: flex;
    margin-right: auto;
    margin-left: auto;
    margin-top: 55px;
    width: fit-content;
    margin-bottom: 35px;

}
.firstname{
    margin-right: 25px;
}
.lastname{
    margin-left: 25px;
}

.address-box{
    width: 890px;
    border-radius: 0;
    padding: 15px;
}
.name-box{
    border-radius: 0;
    padding: 15px;
    width: 403px;

}
.address-setting{
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
}



.zip{
    margin-right: 25px;
}
.state{
    margin-left: 25px;
}
.phone-setting{
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    margin-top: 35px;
}

.phone-box{
    width: 890px;
    border-radius: 0;
    padding: 15px;
}
#profile-settings{
    margin-top: 25px;
    width: 922px;
    border-radius: 0;
    padding: 20px;
    margin-left: auto;
    margin-right: auto;
}
.btn-sett{
    display: flex;
}