.social-links{
    display: flex;
    justify-content: space-around;
    padding: 30px;
    background: #383a60;
    color: #fff;
    margin: auto;
}
.about-me{
    color: #383a60;
}

.social-links a{
    text-transform: none;
    color: #f1f1f1;
    margin: 0 35px 0 0;
    border: 1px solid #909090;
    padding: 10px;
    border-radius: 50%;
    background: #909090;
    transition: all .5s ease ;
}

.social-links a:hover{
    background: #707070;
    border: 1px solid #707070;

}
/* --------------------------- */

.main-footer{
    display: flex;
    flex-direction: row;
    justify-content:space-around;
    padding: 45px 0 25px 0;
}

.second-div{
    display: flex;
    text-align: center;


}

.about{
    width: 30%;
    padding: 45px;
    line-height: 45px;
}

.links{
    border-left: 1px  solid rgba(0, 0, 0, 0.3);
    padding: 45px;
    line-height: 45px;
    color: #505050;
    margin-right: 15rem;


}
.contactus-footer{
    display: flex;
    justify-content: center;
    padding: 35px;
    background: #f1f1f1;
    align-items: center;
    text-align: center;
    flex-direction: column;
    position: relative;
    bottom: -1.5rem;
}
.contactus-footer h2{
    margin: 25px;
    color: #383a60;
    font-size: 30px;
}

.copyright{
    text-align: center;
    padding: 15px;
    background: #383a60;
    position: relative;
    bottom: -1.5rem;
}
.copyright p{
    margin-top: 15px;
    color: #909090;
}
.products{
    border: 2px solid;
    padding: 25px;
}
.contact{
    border-left: 1px  solid rgba(0, 0, 0, 0.3);
    padding: 45px;
    text-align: justify;
    line-height: 45px;
    color: #505050;
}

.linkss{
    text-decoration: none;
    color: #505050;
}

#paymenth1{
    margin-top: 25px;
    color: #383a60;
}
@media screen and (max-width:600px){
    .about{
        display: none;
    }
    .second-div{
        display: none;
    }
}