.navbar-main {
    display: flex;
    justify-content: space-around;
    padding: 10px;
    margin-bottom: 15px;

}


/* .navbar-main:hover{
    
    box-shadow: 0px 1px 8px 0px rgba(0,0,0,0.2);
} */


.navbar-logo h2 {
    font-weight: 900;
    font-size: 30px;
    padding: 15px;
    color: #505050;
}

#logo {
    text-decoration: none;
}

.navbar-link {
    display: flex;

}

.nav-link {
    text-decoration: none;
    color: #505050;
    margin: 15px;
}

.covid-link {
    text-decoration: underline;
    font-weight: 900;
    letter-spacing: -1px;
    color: #000;
}

.covid {
    background: #f1f1f1;
    text-align: center;
    padding: 15px;

}

.dropbtn {
    background-color: transparent;
    color: #505050;
    max-width: 65px;
    border: none;
    padding: 0;
    font-size: 45px;
    cursor: pointer;
}

.dropdown {
    position: relative;
    display: inline-block;

}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 200px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

.dropdown-content a {
    color: #000;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

.dropbtn:hover {
    color: #383a60;
    background: none;
}

.dropdown-content a:hover {
    background-color: #383a60;
    color: #fff;
}

.dropdown:hover .dropdown-content {
    display: block;
}


#username {
    font-size: 20px;
    text-decoration: none;
    color: #505050;
    padding: 19px;
}

.icons-navbar {
    font-size: 25px;
    text-decoration: none;
    color: #505050;
    padding: 19px;
    font-weight: 900;
}

.icons-navbar:hover {
    color: #383a60;
}

.search-dash {
    align-items: center;
    text-align: center;
    padding: 15px;
}

#search-dash {
    width: 500px;
    padding: 15px;
    border-radius: 0;
    margin: 0;
    outline: none;
    transition: all .5s ease;
}

#search-dash:hover {
    box-shadow: inset 0 0 4.5px #383a60;
}

#searchdash-btn {
    width: 55px;
    font-weight: 900;
    padding: 14px;
    outline: none;
    margin: 0;
    border-radius: 0;
}

#Find {
    color: #383a60;
    padding: 15px;
    margin: 10px;
}

#pitch {
    color: #383a99;
}

.bodydash {
    display: flex;
    justify-content: center;
    padding: 25px;
}

.catagories {
    max-width: 25%;
    min-width: 25%;
    height: 40vh;
    border: .5px solid rgba(0, 0, 0, 0.2);

}

.icons-feed {
    display: flex;
}

#arrow {
    transform: rotateX(180deg);
}

.icons-post {
    font-size: 20px;
    margin: 1rem 2rem 0 0;
}

.icons-post:hover {
    /* color: #383a99; */
    cursor: pointer;
}

.feed {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    max-width: 50%;
    min-width: 50%;
    border-radius: 5px;
    margin: 0 25px 0 25px;
    padding-bottom: 25px;
}

.tags {
    max-width: 20%;
    min-width: 20%;
    height: 40vh;
    border: .5px solid rgba(0, 0, 0, 0.2);
}

.tags-text {

    text-align: center;
    padding: 15px;
    color: #383a60;
    font-weight: 500;
}

.mycatagories {
    text-align: center;
    padding: 15px;
    color: #383a60;
    font-weight: 500;

}

.smallhr {
    width: 30%;
    border: .5px solid;
    margin-left: auto;
    margin-right: auto;
}

.pegination {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    padding: 45px 0 10px 0;
    text-align: center;
}


.pegnum {
    padding: 15px;
    text-decoration: none;
    color: #505050;
    margin: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    transition: all .5s ease;
}

.pegnum:hover {
    background: #383a60;
    color: #fff;
    box-shadow: 0 0 2px #383a60;

}

.title-feed {
    text-align: center;
    padding: 15px;
}

.latest-jobs {
    background: #f1f1f1;
    padding: 10px 0 25px 0;
}

.job-title {
    color: #383a60;
    padding-bottom: 25px;
}

.post {
    padding: 25px;
    box-shadow: 0 0 5px #383a60;
    margin: 25px;
    border-radius: 8px;

}

.desc-job {
    font-weight: bold;
    line-height: 55px;
}

.desc-text {
    line-height: 25px;
    color: #505050;
}

.tags-div {
    display: flex;
}

.tag {
    padding: 5px;
    border: 1px solid #383a60;
    margin: 15px 10px 5px 0;
    border-radius: 5px;
    background: #383a60;
    color: #fff;

}