.aboutus-main-container{
    min-height: 80vh;
    margin-top: 3rem;
    display: flex;
    align-items: center;
    justify-items: center;
    background: #fff;

}

.about-pic-sec{
    background: url(../assets/signin2.svg);    
    background-size: contain 55%;
    background-color: #f7f7f7;
    width: 100%;
    background-position: left;
    background-attachment: fixed;
    overflow: hidden;
    background-repeat: no-repeat;
    padding: 100px 0;
}
.inner-container{
    width: 45%;
    float: right;
    opacity: 95%;
    background-color: #fff;
    margin-right: 5rem;
    box-shadow: 0 10px 10px -5px rgba(0, 0, 0, .3);
    padding: 150px;
}

.inner-container > h1{
    margin-bottom: 30px;
    border-bottom: 5px solid #38306a;
    width: 30%;
    padding: 15px;
    font-size: 23px;
    text-transform: uppercase;
    font-weight: 900;
}

.lorem-text{
    font-size: 20px;
    color: #909090;
    line-height: 25px;
    text-align: justify;
    margin-bottom: 40px;

}



.join-us-about{
    display: flex;
    justify-content: space-between;
    font-weight: 700;
    font-size: 13px;
}

.join-us-about > .link{
    text-decoration: none;
    list-style: none;
    color: #505050;
    font-size: 20px;
    border: 1px solid #505050;
    padding: 11px;
    width: 150px;
    text-align: center;
}

.join-us-about > .link:hover{
    background: #383a60;
    color: #fff;
    border: 1px solid #383a60;
}

@media screen and (max-width:1200px){
    .inner-container{
        padding: 80px;
    }
}
@media screen and (max-width:1000px){
    .about-pic-sec{
        background-size: 100%;
        padding: 100px 40px;
    }
    .inner-container{
        width: 100%;
        margin: 0;
        padding: 15px;
    }
}
@media screen and (max-width:800px){
    .inner-container{
        padding: 0px;
    }
    .inner-container > h1{
        width: 100%;
        text-align: center;
        border: none;
        
    }
}

@media screen and (max-width:750px){
    .inner-container{
        padding: 15px;
        margin: 0;
    }
    .inner-container > h1{
        width: 100%;
        text-align: center;
        border: none;
        
    }
    .join-us-about{
        border-bottom: 1px solid rgba(0, 0, 0, .3);
        padding-bottom: 25px;
    }
}
@media screen and (max-width:600px){
    .inner-container{
        width: 100%;
        margin: 0;
        padding: 0;
        border-top: 1px solid rgba(0, 0, 0, .3);

    }
    .about-pic-sec{
        padding: 0;
    }
    .inner-container > h1{
        width: 100%;
        text-align: center;
        border: none;
        
    }
}

.icon{
    position: relative;
    bottom: -5px;
}