.top-branding-settings{
    text-align: center;
    color: #505050;
    padding: 15px;
}

#settings-brand{
    color: #383a90;
}
#link-brand-setting{
    text-decoration: none;
    color: #505050;
}

.settings-navbar{
    margin-top: 35px;
    margin-bottom: 35px;
    display: flex;
    justify-content: center;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    transition: all .5s ease;
}

.links-nav-settings{
    padding: 25px;
    text-decoration: none;
    color: #505050;
    margin-right: 25px;
}

#active{
    background: rgba(0, 0, 0, .05);
}

.links-nav-settings:hover{
    background: rgba(0, 0, 0, .05);
}
.icons-settings{
    position: relative;
    top: 2px;
}
.headline{
    color: #505050;
    text-align: center;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    border-bottom: 3px solid #383a60;
    padding: 5px;
}


.link-card{
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    margin-top: 35px;
}
.card-link{
    color: #505050;
    font-weight: 400;
    margin-top: 35px 0 25px 0;
}
.link-card-btn{
    text-decoration: none;
    padding: 15px 45px;
    color: #fff;
    border: 1px solid #383a60;
    background: #383a60;
    margin: 25px 0 25px 0;
}
.link-card-btn:hover{
    background: #383a90;
    width: 250px;
}
.box-1-payment{
    margin: 35px 0 35px 0;
}