.top-branding-settings{
    text-align: center;
    color: #505050;
    padding: 15px;
}

#settings-brand{
    color: #383a90;
}
#link-brand-setting{
    text-decoration: none;
    color: #505050;
}

.settings-navbar{
    margin-top: 35px;
    margin-bottom: 35px;
    display: flex;
    justify-content: center;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    transition: all .5s ease;
}

.links-nav-settings{
    padding: 25px;
    text-decoration: none;
    color: #505050;
    margin-right: 25px;
}

#active{
    background: rgba(0, 0, 0, .05);
}

.links-nav-settings:hover{
    background: rgba(0, 0, 0, .05);
}
.icons-settings{
    position: relative;
    top: 2px;
}
.headline{
    color: #505050;
    text-align: center;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    border-bottom: 3px solid #383a60;
    padding: 5px;
}

/* password */
.password-boxes{
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
}
.pw-input{
    padding: 15px;
    width: 500px;
    margin: 5px 0 20px 0;
    border-radius: 0;
}

.set-pw{
    color: #505050;
    margin: 35px 0;
    font-weight: 300;
}

.pw-text{
    color: #505050;
    margin-bottom: 10px;
}

.save-pw{
    margin: 20px 0;
    padding: 15px;
    border-radius: 0;
}
.title-2{

    color: #505050;
    margin: 35px 0;
    font-weight: 300;
}
.line-security{
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    margin: 20px 0 20px 0;
}
.twofactor-txt{
    color: #505050;
}
.method-box{
    display: flex;
    padding: 15px;
    font-weight: 700;
    margin-top: 25px;
}

.twofactor{
    position: relative;
    top: -5px;
}