.all-elements-contact {
    text-align: center;
    margin-top: 3%;
    margin-bottom: .1%;
}

.elements-contact-main {
    padding: 10px;
}

.logo-sec h3 {
    color: #383a60;
    text-transform: uppercase;
    margin: 15px;
    font-size: 25px;
}

.input {
    border: 1px solid #383a60;
    padding: 20px;
    margin: 10px 0 10px 0;
    width: 500px;
    background: transparent;
    outline: none;
    border-radius: 5px;
}

.input:focus {
    border: 1px solid dodgerblue;
}

.user-icon {
    font-size: 5em;
    border: .5px solid #383a60;
    border-radius: 50%;
    padding-top: 5px;
    color: #383a60;
}

.elements-contact-social {
    padding: 15px;
    text-align: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

hr {
    width: 300px;
}

.button {
    padding: 20px;
    width: 540px;
    background: #383a60;
    color: #fff;
    border-radius: 5px;
    font-weight: bolder;
    font-size: 1.5em;
    border: 2px solid #383a60;
    transition: all .5s ease;
    margin: 10px;
}

.button:hover {
    background: #303a75;
    cursor: pointer;
}

.elements-contact-social li {
    margin: 10px;
    border: .5px solid;
    width: 310px;
    padding: 10px;
    border-radius: 5px;
    list-style: none;
}

#google {
    background: #fff;
    color: #000;
    border: .5px solid #909090;
    transition: all .5s ease;
}

#facebook {
    background: royalblue;
    color: #fff;
    transition: all .5s ease;
}

#facebook:hover {
    background: #005599;
    cursor: pointer;
}

#google:hover {
    background: transparent;
    cursor: pointer;
}

#github {
    background: #505050;
    color: #fff;
    transition: all .5s ease;
}

#github:hover {
    background: #404040;
    cursor: pointer;

}

#signin-icon {
    position: relative;
    bottom: -4px;
}
.link{
    text-decoration: none;
}
.icons-fa {
    position: relative;
    bottom: -2px;
}

footer {
    position: relative;
    bottom: -11rem;
    padding: 15px;
    border-top: .5px solid rgba(0, 0, 0, 0.15);
    display: inline-flex;
    width: 97.9%;
    justify-content: center;
}

footer li {
    list-style: none;
    margin: 10px;
    font-size: 13px;

}

@media screen and (max-width:600px) {
    footer {
        display: none;
    }

}

li {
    list-style: none;
}

img {
    height: 60px;
    margin-top: 1rem;
    margin-bottom: -4rem;
}

.agree-note {
    color: #909090;
    font-size: 13px;
    margin: 0;
    line-height: 20px;
}

.agree-note span {
    color: royalblue;

}

textarea {
    margin: 10px;

}