.aboutus-banner-div{
    display: flex;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    
}

.text-div-about-banner{
    margin: 30% 5rem 0 0;
    height: fit-content;
    font-size: 25px;
    color: #909090;
    padding: 15px;
}

.about-banner-div{
    margin: 25px 0 0 35px;
    padding: 2rem 0 0 20px;
    height: 600px;
    width: 600px;
    
    background: url(../assets/aboutus.svg);
    background-repeat: no-repeat;
    background-size: contain;
}
.meetteamheading{
    color: #707070;
