
.brandname{
    color: #383a90;
}
.link-brand{
    text-decoration: none;
    color: #505050;
    padding: 0 0 5px 0;
    text-align: center;
}
.cardlink-main-container{
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    padding: 5px;
}

.form-main{
    padding: 35px;
    border-radius: 10px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

.inputbox-card{
    border-radius: 0;
    margin-top: 25px;
    padding: 15px;
}
.pay{
    margin-left: auto;
    margin-right: auto;
    padding: 15px;
}

.enter{
    padding: 15px;
    text-align: center;
    color: #505050;
    font-weight: 300;
}