h1 {
    text-align: center;
    font-family: serif;
    font-size: 3em;
    color: #bbb,
}

#team {
    display: flex;
    flex-wrap: wrap;
    flex-grow: 3;
    justify-content: space-around;
}

.card {
    width: 33%;
    width: 450px;
    height: 450px;
    overflow: hidden;
    position: relative;
    margin: 0 0.5% 1% 0;
    border-radius: 10px;
    display: inline-block;
}

.card:nth-of-type(3n) {
    margin-right: 0;
}

.card img {
    width: 100%;
    min-height: 100%;
    transition: all 0.9s;
}

.card:hover img {
    transform: scale(1.2, 1.2);
}

.data {
    position: absolute;
    bottom: 5%;
    background: #383a60;
    text-align: center;
    width: 90%;
    color: #bbb;
    height: 80px;
    overflow: hidden;
    left: 0;
    border-radius: 10px;
    right: 0;
    margin: 0 auto;
    padding: 10px 5%;
    box-sizing: border-box;
    opacity: 0.8;
    transition: all 0.4s;
}

.card:hover .data {
    width: 100%;
    height: 100%;
    bottom: 0;
    opacity: 0.9;
    padding: 90px 10%;
}

.data h2 {
    margin: 0 0 5px 0;
    transition: color 0.4s;
}

#teamheading{
    color: #909090;
}

.data p {
    opacity: 0;
    text-align: justify;
    transition: all 0.2s;
}

.card:hover .data p,
.card:hover .data a {
    opacity: 1;
}

.data a {
    color: #333;
    text-decoration: none;
    padding: 20px;
    opacity: 0;
}

.data a:hover,
.card:hover h2 {
    color: #fff;
}

@media (max-width:799px) {
    .card {
        width: 100%;
        margin-right: 0;
    }
}
