.index-main-container{
    margin-bottom: 25px;
}
nav {

    height: 8vh;
    margin-bottom: 2px;
    position: absolute;
    text-align: center;
    background: rgba(0, 0, 0, .01);
    width: 100%;
    z-index: 90;
    color: #ffffff;

}

.gap{
    height: 30vh;
    background: #fff;
}

.logo {
    text-decoration: none;
    color: #fff;
    position: relative;
    top: 15px;
    font-size: 25px;
}


.banner-section {
    z-index: -1;
    height: 100vh;
    background: linear-gradient(rgba(56, 58, 96, 5), rgba(144, 144, 144, .5)), url("https://www.linkedin.com/dms/C4E06AQGAc1vJqCBgbQ/messaging-attachmentFile/0?m=AQIVMATqRjXt7AAAAXQctQHsB9o0erb-F6gqw4984BlRw2qbzSfEejD51g&ne=1&v=beta&t=9vPODRibODzkq47CoOzd7ZcxEitpnZ5kNAbRG__82GY#S6703375049463779328_500");
    /* background: url(https://www.linkedin.com/dms/C4E06AQGAc1vJqCBgbQ/messaging-attachmentFile/0?m=AQIVMATqRjXt7AAAAXQctQHsB9o0erb-F6gqw4984BlRw2qbzSfEejD51g&ne=1&v=beta&t=9vPODRibODzkq47CoOzd7ZcxEitpnZ5kNAbRG__82GY#S6703375049463779328_500); */
    background-repeat: no-repeat;clip-path: polygon(
        0 0,
        100% 0,
        100% 100%,
        0 calc(100% - 5vw)
      );
    background-size: cover;
    background-attachment: fixed;
}

.text-elements {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.text-elements h1{
    color: #303030;
    font-size: 3em;
    text-align: center;

}
#searchbox{
    border: 3px solid #303030;
    padding: 20px;
    width: 400px;
}
#searchbox::placeholder{
    color: #f1f1f1;
}
#search-button{
    border-left: none;
    margin-left: 0;
    background: #303030;
    border: 3px solid #303030;
    padding: 20px;
    width: 150px;
    margin: 5px;
}

#search-button:hover{
    background: #000;
    text-decoration-color: #fff;
    border: 3px solid #000;
}