.services{
    background: f2f2f2;
    text-align: center;
}

.services h1{
    display: inline-block;
    text-transform: uppercase;
    border-bottom: 4px solid #383a60;
    font-size: 20px;
    padding-bottom: 10px;
    margin-top: 4rem;
}

.elements-service{
    max-width: 1200px;
    margin: auto;
    overflow: hidden;
    padding: 20px;
}

.service{
    display: inline-block;
    width: calc(100% / 3);
    margin: 3rem -6px 5px -5px;
    padding: 30px;
    line-height: 30px;
    box-sizing: border-box;
    cursor: pointer;
    transition: All .5s ease;
    outline: hidden;
    border-right: 1px solid rgba(0, 0, 0, .1);
}
#box-none{
    border-right: none;
}
#box-none2{
    border-right: none;
}


.service h2{
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 500;
    margin: 0;

}

.service p{
    color: #909090;
    font-size: 15px;
    font-weight: 500;
}

@media screen and (max-width:800px){
    .service{
        width: 50%;
    }
}

@media screen and (max-width: 500px){
    .service{
        width: 100%;
    }
}


.icons-defn{
    color: #383a60;
    font-size: 55px;
    
}