#pitchlab-branding-color{
    color: #383a90;
}

#logobranding{
    text-decoration: none;
    color: #505050;
}

.pitchlab-branding{
    text-align: center;
    padding: 15px;
}

.main-elements-container-top{
    box-shadow: 0 0 15px rgba(0, 0, 0, .1);
    border-radius: 10px;
    display: flex;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 35px;
    
}
#profile-image{
    height: 300px;
    width: 300px;
    margin: 15px;
    border: 1px solid rgba(0, 0, 0, .2);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .3);
}
.details-div{
    margin: 25px;
}
.Job-details{
    color: #808080;
    padding: 5px 10px 15px 15px;
    font-weight: 400;
}
.person-details{
    color: #505050;
    padding: 5px 10px 15px 15px;
    font-size: 25px;
}
.rating{
    padding: 15px;

}

.stars{
    text-decoration: none;
    color: #808080;
    padding-right: 5px;
    transition: all .5s ease;
}
.stars:hover{
    color: gold;
    
}
.reviewcount{
    color: #505050;
    padding: 5px 0 0 0;
}
.det-elemts{
    padding: 15px;
    line-height: 30px;
}

.dets{
    color: #505050;
}
.icon-prof-det{
    position: relative;
    top: 3px;
}

#edit-profile{
    position: relative;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: 25px;
    margin-right: 25px;
    height: 50px;
    width: 160px;
    font-size: 18px;
    border-radius: 0;
}
.verification-title{
    text-align: center;
    color: #383a60;
    margin-top: 35px;
}

.main-elements-container-verificatons{
    margin: 25px 0 25px 0;
    padding: 15px;
    box-shadow: 0 0 15px rgba(0, 0, 0, .1);
    border-radius: 10px;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
}
.line{
    border: 2px solid #383a60;
    width: 150px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5px;
}
.verif{
    color: #505050;
    line-height: 25px;
    padding: 10px;
}
.icon-prif-deta{
    position: relative;
    top: 2px;
    margin-right: 10px;
}
.checks{
    font-size: 20px;
}
.clickable-verify{
    padding: 15px;
    margin-left: auto;
}
.verify{
    padding: 15px;
    line-height: 40px;
    text-decoration: none;
    color: royalblue;
}

.experience-title{
    text-align: center;
    color: #383a60;
    margin-top: 35px;
}
.main-elements-container-experience{
    display: flex;
    padding: 15px;
    box-shadow: 0 0 15px rgba(0, 0, 0, .1);
    border-radius: 10px;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 25px;
    justify-content: space-between;
}
.experiencebtn{
    width: 150px;
    border-radius: 5px;

}
.experience-det{
    padding: 15px;
    color: #505050;
}