.top-branding-settings{
    text-align: center;
    color: #505050;
    padding: 15px;
}

#settings-brand{
    color: #383a90;
}
#link-brand-setting{
    text-decoration: none;
    color: #505050;
}

.settings-navbar{
    margin-top: 35px;
    margin-bottom: 35px;
    display: flex;
    justify-content: center;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    transition: all .5s ease;
}

.links-nav-settings{
    padding: 25px;
    text-decoration: none;
    color: #505050;
    margin-right: 25px;
}

.links-nav-settings:hover{
    background: rgba(0, 0, 0, .05);
}
.icons-settings{
    position: relative;
    top: 2px;
}
.headline{
    color: #505050;
    text-align: center;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    border-bottom: 3px solid #383a60;
    padding: 5px;
}

/* email-setting */

.boxes1{
    display: flex;
    margin-right: auto;
    margin-left: auto;
    margin-top: 55px;
    width: fit-content;
    margin-bottom: 5px;
    padding: 10px;

}
.emailaddress{
    margin-right: 25px;
}
.confirmemail{
    margin-left: 25px;
}

.updateemail{
    width: 250px;
    border-radius: 0;
    padding: 15px;
    margin-right: auto;
}

.email-box{
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
}

.line-divider{
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    margin: 25px 0 25px 0;
}
.title-1{
    padding: 10px;
    color: #505050;
    font-weight: 350;
}

.notifications-setting{
    display: flex;
    padding: 10px;
    color: #505050;
    line-height: 33px;
    width: 100%;
    justify-content: space-between;
}

.savesettings{
    border-radius: 0;
    padding: 15px;
    width: 200px;
    margin: 5rem 0;
}
#active{
    background: rgba(0, 0, 0, .05);
}

