.customers-container {
    margin: 0;
}

#customers {
    color: #383a60;
    font-size: 25px;
    text-align: center;
    margin: 35px 0 25px 0;
}

.elements-cos {
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    padding: 5px 0 35px 0;
}


.image {
    height: 200px;
    width: 200px;
    margin-bottom: .5rem;
    border: 10px solid #383a60;
    padding: 10px;
    border-radius: 50%;
    transition: all .5s ease;
}

.image:hover {
    box-shadow: inset 0 0 25px #383a60;
}

@media screen and (max-width:750px) {
    .elements-cos {
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
        text-align: center;
    }
}